
"use client";
import React from "react";
import Input from "@/components/componentTag/input"
import Button from "@/components/componentTag/button"

export default function SignUpPage() {
    return (
        <div
            className="min-h-screen bg-gray-100 flex items-center justify-center">
            <div 
                className="grid place-items-center justify-center pt-20"
            >
                <header 
                    className="text-2xl mb-4 font-semibold"
                >
                    Sign Up
                </header>
                <Input 
                    type="email" 
                    label="Email"
                    placeholder="Email" 
                    className="mb-3 w-full"
                    required={true}
                />
                <div
                    className="w-full flex gap-2 mb-3"
                >
                    <Input 
                        type="password" 
                        placeholder="First Name"
                        className="w-full" 
                        label="First Name"
                        required={true}
                    />
                    <Input 
                        type="password" 
                        placeholder="Last Name"
                        className="w-full"
                        label="Last Name" 
                        required={true}
                    />
                </div>
                <Input 
                    type="password" 
                    label="Password"
                    placeholder="Password" 
                    className="mb-3 w-full"
                    required={true}
                />
                <div 
                    className='w-full flex flex-row items-center'
                >
                    <Button 
                        label="Sign Up"
                        className="w-full mt-4 mr-2"
                    />
                </div>
                <div className='flex gap-2 mt-4'>
                    <p>Already have an account?</p>
                    <a 
                        href="/auth/signin"
                        className='text-blue-500 hover:underline'
                    >
                         Sign In
                    </a>
                </div>
            </div>
        </div>
    )
}